import React from 'react';
import styled from 'styled-components';
import {size, palette} from 'styled-theme';
import {Link} from 'components';
import {Tabs, Tab} from 'material-ui/Tabs';
import Paper from 'material-ui/Paper';
import Badge from 'material-ui/Badge';

const Wrapper = styled.div `
.colorTab{
	/*margin-top:20px;*/
}
.colorTab > div:first-child{
	background-color: ${palette('grayscale', 1)} !important;
}
.colorTab > div:nth-child(2) div{
	background-color: ${palette('secondary', 0)} !important;
}
.colorTab > div > button{
  color: ${palette('grayscale', 6)} !important;
  font-weight: 700 !important;
  &:not(:last-child){
    border-right: 2px solid ${palette('grayscale', 1)} !important;
  }
}
`
class FileTab extends React.Component {

  constructor(props) {
    super(props);
  }

  changeTab(tab) {
    // console.log(tab.props['data-route']);
		window.location.href = tab.props['data-route'];

  }

  render() {

		console.log(this.props);
    return (
      <Wrapper>
        <Tabs initialSelectedIndex={0} className="colorTab">
          <Tab label="Resumen"  data-route={`/file/summary/${this.props.id}`} onActive={this.changeTab} >
            {this.props.name == 'summary'
              ? this.props.content
              : ''}
          </Tab>
          <Tab label="Detalles" data-route={`/file/detail/${this.props.id}`} onActive={this.changeTab}></Tab>
          <Tab label="ImÃ¡genes" data-route={`/file/images/${this.props.id}`} onActive={this.changeTab}></Tab>
          <Tab label="Mapas" data-route={`/file/maps/${this.props.id}`} onActive={this.changeTab}></Tab>
          <Tab label="Comunidad" data-route={`/file/community/${this.props.id}`} onActive={this.changeTab}></Tab>
          <Tab label="Comentarios" data-route={`/file/comments/${this.props.id}`} onActive={this.changeTab}></Tab>
        </Tabs>
      </Wrapper>
    )
  }
}

export default FileTab;
